import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { Input } from '../common/FormControls/FormControls';
import s from './FindUsersForm.module.css';

const FindUsersForm = (props) => {
    return (
        <form>
            <div>
                <div className={s.searcher}>
                    <Field className={s.field} onChange={props.findUsers} component={Input} name={"userName"} placeholder='Type to search users' />
                </div>
            </div>
        </form>
    )
}

export default reduxForm({
    form: 'findUsers'
})(FindUsersForm);