import eminem from './../assets/audio/eminem - gnat.mp3';

let initialState = {
    songs: [
        {
            title: "GNAT",
            artist: "Eminem",
            img_src: "./images/music/MTBMB.png",
            src: "eminem - gnat.mp3"
        },
        {
            title: "Song 2",
            artist: "Artist 2",
            img_src: "./music/MTBMB.png",
            src: eminem
        },
        {
            title: "Song 3",
            artist: "Artist 3",
            img_src: "./music/MTBMB.png",
            src: eminem
        },
        {
            title: "Song 4",
            artist: "Artist 4",
            img_src: "./music/MTBMB.png",
            src: eminem
        }
    ],
    currentSongIndex: 0,
    nextSongIndex: 0,
    isPlaying: false
}

const MusicReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SET-IS-PLAYING':
            return {
                ...state,
                isPlaying: !action.isPlaying
            }
        case 'SET-CURRENT-SONG-INDEX':
            return {
                ...state,
                currentSongIndex: action.currentSongIndex + 1 > state.songs.length - 1 ? 0 : action.currentSongIndex + 1
            }

        default:
            return state;
    }
}
export const setCurrentSongIndex = (currentSongIndex) => ({ type: 'SET-CURRENT-SONG-INDEX', currentSongIndex });

export const setIsPlaying = (isPlaying) => ({ type: 'SET-IS-PLAYING', isPlaying });

export default MusicReducer;