import React from 'react';
import {reduxForm} from 'redux-form';
import {createField, Input} from '../common/FormControls/FormControls';
import {required} from '../../Utils/Validators/validators';
import s from './../common/FormControls/FormControls.module.css';
import l from './LoginForm.module.css';

const LoginForm = ({handleSubmit, error, captchaUrl, open}) => {
    if(!open) 
        return null;
        
    return (
        <div className={l.loginForm}>
            <form onSubmit={handleSubmit} >
                <h1>
                    Login
                </h1>
                <div className={l.emailBlock}>
                    {createField("Email", "email", [required], Input, l.email)}
                </div>
                <div className={l.passwordBlock}>
                    {createField("Password", "password", [required], Input, { type: "password" }, l.password)}
                </div>
                <div className={l.rememberMeBlock}>
                    {createField(null, "rememberMe", [], Input, { type: "checkbox" }, null, "Remember me")}
                </div>
                {captchaUrl && <img src={captchaUrl} />}
                {captchaUrl &&
                    <div className={s.captchaBlock}>
                        {createField("Enter symbols from image", "captcha", [required], Input, null)}
                    </div>
                }
                {error && 
                <div className={s.formSummaryError}>
                    {error}
                </div>}
                <div className={l.loginBtn}>
                    <button>Login</button>
                </div>
            </form>
        </div>
    )
}

export default reduxForm({
    form: 'login'
})(LoginForm); 