import React, { useEffect, useState } from 'react';
import Player from './Player';
import audio from './../../assets/audio/eminem - gnat.mp3';

let PlayerState = () => {
    const [songs] = useState([
        {
            title: "GNAT",
            artist: "Eminem",
            img_src: "https://hiphop4real.com/wp-content/uploads/2020/12/m1000x1000-2020-12-18t105517.448.jpg",
            src: audio
        },
        {
            title: "Song 2",
            artist: "Artist 2",
            img_src: "https://hiphop4real.com/wp-content/uploads/2020/12/m1000x1000-2020-12-18t105517.448.jpg",
            src: "./../../assets/audio/eminem - gnat.mp3"
        },
        {
            title: "Song 3",
            artist: "Artist 3",
            img_src: "./images/music/MTBMB.png",
            src: "./audio/eminem - gnat.mp3"
        },
        {
            title: "Song 4",
            artist: "Artist 4",
            img_src: "./images/music/MTBMB.png",
            src: "./audio/eminem - gnat.mp3"
        }
    ]);

    const [currentSongIndex, setCurrentSongIndex] = useState(0);
    const [nextSongIndex, setNextSongIndex] = useState(0);

    useEffect(() => {
        setNextSongIndex(() => {
            if (currentSongIndex + 1 > songs.length - 1) {
                return 0;
            } else {
                return currentSongIndex + 1;
            }
        });
    }, [currentSongIndex]);

    return (
        <Player
            currentSongIndex={currentSongIndex}
            setCurrentSongIndex={setCurrentSongIndex}
            nextSongIndex={nextSongIndex}
            songs={songs}
        />
    );
}

export default PlayerState;
