import React from 'react'
import s from './PlayerInfo.module.css';

function PlayerInfo(props) {
    return (
        <div className={s.cPlayerInfo}>
            <div className={s.detailsImg}>
                <img src={props.song.img_src} alt="" />
            </div>
            <h3 className={s.detailsTitle}>{props.song.title}</h3>
            <h4 className={s.detailsArtist}>{props.song.artist}</h4>
        </div>
    )
}

export default PlayerInfo;